.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-size: cover;
    background-position: center;
    padding: 20px;
    /* 增加內邊距確保內容不會靠邊框太近 */
}

.mapContainer {
    position: relative;
}

.mapImage {
    width: 40%;
    max-width: 600px;
    height: auto;
}

.toggleButtonContainer {
    margin-top: 20px;
}

.buttonsContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
    gap: 10px;
    flex-wrap: wrap;
    /* 在小屏幕上自動換行 */
}

.map-button,
.audio-button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    white-space: nowrap;
    transition: background-color 0.3s ease;

    &:hover {
        background-color: #45a049;
    }
}

.resultContainer {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.input {
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 80%;
    max-width: 300px;
}

/* 響應式設計 */
@media (max-width: 768px) {
    .mapImage {
        width: 60%;
        /* 更小屏幕上增加地圖圖片寬度 */
    }

    .map-button,
    .audio-button {
        padding: 6px 12px;
        font-size: 14px;
        /* 調整按鈕大小以適應小屏幕 */
    }
}

@media (max-width: 480px) {
    .mapImage {
        width: 60%;
        /* 更小屏幕上增加地圖圖片寬度 */
    }

    .map-button,
    .audio-button {
        padding: 6px 12px;
        font-size: 12px;
        /* 調整按鈕大小以適應更小的屏幕 */
    }
}